body {
  display: flex;
  height: fit-content;
  width: 100vw;
  margin: 0px;
  background-image: linear-gradient(
    to bottom right,
    black,
    rgb(12, 12, 12),
    rgb(32, 32, 32),
    gold
  );
  background-repeat: no-repeat;
}

.App {
  display: flex;
  height: fit-content;
  width: 100vw;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
